.list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.list li {
  padding: 5px 0;
  margin-bottom: 10px;
  border-bottom: 1px solid #ccc;
}
.list li b {
  display: inline-block;
  margin-right: 15px;
}
.list svg {
  margin-right: 10px;
}



